import React from "react";
import "./features.scss";
import { Feature } from "./components";
import { BsCardText } from "react-icons/bs";
import { GiMeal } from "react-icons/gi";
import { RiRunLine } from "react-icons/ri";
import { AiOutlineBarChart } from "react-icons/ai";
export interface Props {}

const Features: React.FC<Props> = () => {
  return (
    <section className="features">
      <h2 className="features__title">Moliwoci</h2>
      <Feature
        icon={<BsCardText />}
        title="Plan treningowy"
        text="Zaprojektuj zestaw wicze do swojej aktualnej formy i pozwoli osign
        wymarzon sylwetk oraz zadba o zdrowie."
      />
      <Feature
        icon={<GiMeal />}
        title="Zaplanuj diete"
        text="Prawidowa dieta jest kluczowa dla zdrowia, kondycji i wietnego samopoczucia."
      />
      <Feature
        icon={<RiRunLine />}
        title="Motywacja"
        text="W osigniciu sukcesu najwaniejsze jest to, co dzieje si w gowie. Trenuj regularnie i patrz jak ronie Twoja pozycja w rankingu."
      />
      <Feature
        icon={<AiOutlineBarChart />}
        title="Monitorowanie rozwoju ciala"
        text="Wykresy to najlepszy przyjaciel sportowca. Dziki aplikacji moesz led
        przyrost swojej siy!"
      />
    </section>
  );
};

export default Features;
